.App {
  height: 100%;
  width: calc(100% - 50px);
  max-width: 1130px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 80px;
  padding: 22px 25px;
  background-color: white;
  border-radius: 5px;
}

h1 {
  font-size: 26px;
  font-weight: bold;
}

span {
  text-align: center;
  margin-bottom: 20px;
  display: block;
}

.Header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.filters {
  display: flex;
  column-gap: 30px;
}
.filters .DropDown {
  display: flex;
  align-items: center;
}
.filters .DropDown label {
  margin-right: 7px;
}

.SearchBar {
  display: flex;
  width: 100%;
  height: 34px;
  margin: 15px 0 26px;
  column-gap: 20px;
}

input {
  width: 100%;
  height: 100%;
}

.Button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px 20px;
  text-wrap: nowrap;
  height: 34px;
  border-radius: 3px;
  background-color: #081378;
  color: #F3F3F3;
  font-weight: normal;
  font-size: 13px;
  cursor: pointer;
}
.Button:hover {
  box-shadow: 0px 0px 14px 0px rgba(8, 19, 120, 0.5);
}
.Button:active {
  background-color: rgb(32, 49, 206);
}

.CardsCollection {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 30px 10px;
  margin-bottom: 30px;
}

.Card {
  display: flex;
  max-width: 558px;
  width: 100%;
  border-radius: 3px;
  background: #F3F3F3;
  /* box-shadow: 0px 0px 14px 0px rgba(8, 19, 120, 0.10); */
  cursor: pointer;
}
.Card:hover {
  box-shadow: 0px 0px 14px 0px rgba(8, 19, 120, 0.50);
}
.Card img {
  border-radius: 3px 0 0 3px;
  width: 146px;
  height: 210px;
}
.Card .content {
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.Card h1 {
  color: #081378;
  font-size: 19px;
  font-weight: bold;
}
.tag {
  padding: 3px 7px;
  background-color: #081378;
  color: #F3F3F3;
  border-radius: 4px;
  display: inline-block;
  font-size: 12px;
}
.Card .info p {
  margin-top: 10px;
  font-size: 16px;
  opacity: 0.5;
}

.BookPage {
  position: relative;
  width: 100%;
}

.BookPage .Button {
  position: absolute;
  left: 0;
}

.bookInfo {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  column-gap: 40px;
}

.bookInfo img {
  max-width: 300px;
  width: 100%;
  height: auto;
  box-shadow: 0px 0px 14px 0px rgba(8, 19, 120, 0.2);
}

.bookInfo .content {
  /* padding: 0 70px 0 40px; */
  width: 100%;
  max-width: 750px;
}

.bookInfo h1 {
  margin: 30px 0 15px;
}

.tagsCollection {
  width: 100%;
  display: flex;
  column-gap: 10px;
  margin-bottom: 20px;
}

.tagsCollection + p {
  font-size: 16px;
  opacity: 0.5;
}

.Loader {
  animation: rotate 2s infinite;
  height: 50px;
  width: 50px;
}

.Loader:before,
.Loader:after {
  border-radius: 50%;
  content: "";
  display: block;
  height: 20px;
  width: 20px;
}
.Loader:before {
  animation: ball1 2s infinite;
  background-color: #6674F3;
  box-shadow: 30px 0 0 #081378;
  margin-bottom: 10px;
}
.Loader:after {
  animation: ball2 2s infinite;
  background-color: #081378;
  box-shadow: 30px 0 0 #6674F3;
}

@keyframes rotate {
  0% { transform: rotate(0deg) scale(0.8) }
  50% { transform: rotate(360deg) scale(1.2) }
  100% { transform: rotate(720deg) scale(0.8) }
}

@keyframes ball1 {
  0% {
    box-shadow: 30px 0 0 #081378;
  }
  50% {
    box-shadow: 0 0 0 #081378;
    margin-bottom: 0;
    transform: translate(15px, 15px);
  }
  100% {
    box-shadow: 30px 0 0 #081378;
    margin-bottom: 10px;
  }
}

@keyframes ball2 {
  0% {
    box-shadow: 30px 0 0 #6674F3;
  }
  50% {
    box-shadow: 0 0 0 #6674F3;
    margin-top: -20px;
    transform: translate(15px, 15px);
  }
  100% {
    box-shadow: 30px 0 0 #6674F3;
    margin-top: 0;
  }
}

.ErrorPage {
  background-color: #F3F3F3;
  width: calc(100% - 100px);
  padding: 170px 50px 40px;
  border-radius: 20px;
  font-size: 40px;
  font-weight: bold;
  color: #081378;
}